              <!-- <v-img :src="require('@/assets/ImagesFuzzy/fuzzy_sort.gif')" /> -->
              <v-img src="/ImagesFuzzy/fuzzy_sort.gif" />
            </v-card-text>
          </v-card>
        </v-col>
        <v-col cols="12" md="3">
          <v-card>
            <v-card-title>Insertion Sort</v-card-title>
